.songlistWrapper {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding-top: 1em;
  padding-bottom: 1em;

  .list {
    .songbox {
      display: flex;
      justify-content: space-around;
      align-items: center;

      .songpic {
        display: flex;
        align-items: center;
        justify-content: left;
      }

      div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 13em;
        // background-color: palegreen;
        // background-color: palegoldenrod;
        color: rgb(207, 207, 207);
        height: 40px;
        img {
          height: 40px;
          margin-top: 5px;
          margin-bottom: 5px;
        }

        .songename {
          margin-left: 10px;
        }
      }
    }

    hr {
      margin: 0.5em 3em;
      background-color: rgb(121, 121, 121);
    }
  }
}

@media (max-width: 783px) {
  .songlistWrapper .list .songbox div img {
    display: none;
  }
}

@media (max-width: 624px) {
  .songlistWrapper .list .songbox .singer {
    display: none;
  }

  .songlistWrapper .list .songbox  .songpic{
    justify-content: center;
  }

  .songlistWrapper .list .songbox .time {
    display: none;
  }
}
